import Vue from 'vue'
import Router from 'vue-router'
import AppView from '@/components/app-view'
import Home from '@/pages/home'
Vue.use(Router)
const page = name => () => import('@/pages/' + name)

export default new Router({
  // mode: 'history',
    routes: [
        {
            path: '',
            component: AppView,
            children: [
                {path: '/', name: 'home', component: Home},
                {path: '/user-table',name:'user-table', component:page('userTable/userTable')},
                {path: '/user-table-before-tabs',name:'user-table-before-tabs', component:page('userTableBefore/userBeforeTableTabs')},
                {path: '/custom-table', name: 'custom-table', component: page('customTable/index')},
                {path: '/basic-chart', name: 'basic-chart', component: page('chartInfo/basicChart')},
                {path: '/other-chart', name: 'other-chart', component: page('chartInfo/otherChart')},
                {path: '/editor', name: 'editor', component: page('editor/index')},
                {path: '/high-light', name: 'high-light', component: page('highLight/highLight')},
                {path: '/word-cloud', name: 'word-cloud', component: page('wordCloud/wordCloud')},

                {path: '/keyboard', name: 'c-keyboard', component: page('c-keyboard')},
                {path: '/data-table', name: 'p-data-table', component: page('table')},
                {path: '/demo', component: page('demo')}
            ]
        },
        {path: '/login', name: 'login', component: page('login')},
        // pages
        {path: '/home-login', name: 'p-login', component: page('home-login')},
        {path: '/register', name: 'p-register', component: page('register')},
        {path: '/keyboard', name: 'p-keyboard', component: page('c-keyboard')}
    ]
})



